.p-privacy {
  max-width: 850px;
  margin: 150px auto 0;
  color: #777;
}

@media (max-width: 896px) {
  .p-privacy {
    margin: 100px auto 0;
  }
}

.p-privacy .-box-subscribe .inner {
  background: none;
}

@media (min-width: 897px) and (max-width: 1439px) {
  .c-privacy {
    padding: 0 30px;
  }
}

@media (max-width: 896px) {
  .c-privacy {
    padding: 0 30px;
  }
}

.c-privacy__heading {
  font-size: 2.2rem;
  color: #777;
  text-align: center;
  position: relative;
  z-index: 1;
}

@media (max-width: 896px) {
  .c-privacy__heading {
    font-size: 1.8rem;
  }
}

.c-privacy__heading .text {
  font-size: 2.2rem;
  color: #777;
  text-align: center;
  padding: 0 20px;
  background: #fff;
  display: inline-block;
  position: relative;
  z-index: 3;
}

@media (max-width: 896px) {
  .c-privacy__heading .text {
    font-size: 1.8rem;
  }
}

.c-privacy__heading::before {
  content: '';
  width: 100%;
  border-top: 5px dotted #DCDCDC;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  z-index: 1;
}

@media (max-width: 896px) {
  .c-privacy__heading::before {
    border-top: 2px dotted #DCDCDC;
  }
}

.c-privacy__subheading {
  font-size: 4rem;
  text-align: center;
  color: #211E1F;
}

@media (max-width: 896px) {
  .c-privacy__subheading {
    font-size: 3.2rem;
  }
}

.c-privacy__subheading .c-keyword {
  color: #211E1F;
}

.c-privacy__subheading .c-search-result-number {
  color: #FFC416;
  padding-left: 10px;
}

.c-privacy__content {
  color: #777;
  margin-top: 30px;
}

@media (max-width: 896px) {
  .c-privacy__content {
    margin-bottom: 50px;
    padding: 0 30px;
  }
}

.c-privacy__content .c-heading-04 {
  color: #211E1F;
}

.c-privacy__content .c-heading-04 + .c-text-cmn-01 {
  margin-top: 20px;
}

.c-privacy__content .c-heading-04 + .c-privacy-list {
  margin-top: 20px;
}

.c-privacy__content .c-heading-04:not(:first-of-type) {
  margin-top: 40px;
}

@media (max-width: 896px) {
  .c-privacy__content .c-text-cmn-01 {
    font-size: 1.4rem;
  }
}

.c-privacy__content .c-text-cmn-01 + .c-text-cmn-01 {
  margin-top: 10px;
}

.c-privacy-list + .c-text-cmn-01 {
  margin-top: 10px;
}

.c-privacy-list__item {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
}

.c-privacy-list__item .no-step-01 {
  width: 30px;
  margin-left: 20px;
  color: #211E1F;
}

.c-privacy-list__item .text {
  width: auto;
  flex: 1;
}

.c-privacy-list__item .text .strong {
  color: #211E1F;
}

.c-privacy-list__sub-item {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
}

.c-privacy-list__sub-item .no-dot {
  color: #211E1F;
  margin-right: 10px;
}

.c-privacy-list__sub-item .sub-text {
  width: auto;
  flex: 1;
}

.c-privacy-list__sub-item .sub-text .strong {
  color: #211E1F;
}

.c-privacy-box-bg {
  padding: 40px 60px;
  border-radius: 20px;
  background: #FCFBF2;
  margin-top: 20px;
}

@media (max-width: 896px) {
  .c-privacy-box-bg {
    padding: 20px;
  }
}

.c-privacy-box-bg__inner {
  display: flex;
  flex-wrap: wrap;
}

.c-privacy-box-bg__inner-box {
  width: 50%;
}

@media (max-width: 896px) {
  .c-privacy-box-bg__inner-box {
    width: 100%;
  }
}

@media (max-width: 896px) {
  .c-privacy-box-bg__inner-box:not(:first-of-type) {
    border-top: 1px solid #DCDCDC;
    margin-top: 20px;
  }
}

.c-privacy-box-bg__inner-box > .heading {
  padding: 10px 0;
  border-bottom: 1px solid #DCDCDC;
  color: #211E1F;
}

@media (max-width: 896px) {
  .c-privacy-box-bg__inner-box > .heading {
    border-bottom: 0;
  }
}

.c-privacy-box-bg__inner-box > .content {
  padding-top: 5px;
}

.c-privacy-box-bg__inner-box > .content ol {
  list-style: decimal;
  padding-left: 15px;
}

.c-privacy-box-bg__inner-box > .content ol li {
  padding-left: 5px;
}

.c-privacy-box-bg__inner-box > .content ol li:not(:first-of-type) {
  margin-top: 5px;
}

.c-privacy-box-note {
  text-align: center;
  margin-top: 20px;
  font-size: 1.4rem;
  font-weight: 300;
}
